@import url('https://fonts.googleapis.com/css2?family=Heebo:wght@900&family=Roboto&family=Share+Tech+Mono&display=swap');

body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
        'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
        sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
        monospace;
}

a {
    text-underline-offset: 4px;
}

.page {
    position: relative;
    width: 100%;
    height: 100%;
}

.page-child {
    position: absolute;
    width: 720px;
    height: 1080px;
    color: white;
    font-family: 'Roboto', sans-serif;
    font-weight: 900;
    overflow: visible;
}

h1 {
    margin-top: 0;
    margin-bottom: 50px;
    padding: 0;
    font-family: 'Heebo', sans-serif;
    font-size: 102px;
    line-height: 130px;
    text-align: left;
}

h3 {
  margin-top: 0;
}

.work-title {
    padding: 28px;
    border: 2px solid white;
    font-family: 'Share Tech Mono', monospace;
    font-size: 35px;
    font-weight: 100;
    letter-spacing: 4px;
    margin-bottom: 20px;
    border-radius: 10px;
}

.work-type {
    font-family: 'Share Tech Mono', monospace;
    margin-top: 50px;
    font-size: 30px;
    font-weight: 100;
    line-height: 40px;
    margin-bottom: 0;
}

.skill {
    font-family: 'Share Tech Mono', monospace;
    font-size: 30px;
    font-weight: 100;
    margin-top: 30px;
    flex: 0 0 50%;
}

.experience {
    font-family: 'Share Tech Mono', monospace;
    font-size: 30px;
    font-weight: 100;
    flex: 0 0 50%;
}

.experience:first-child {
    margin-top: 30px;
}

.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 30px;
  margin-bottom: -5px;
  font-family: 'Share Tech Mono', monospace;
}

.pagination button {
  cursor: pointer;
  background: none;
  border: none;
  color: white;
  font-size: 35px;
  line-height: 35px;
  outline: none;
}

.pagination button:hover {
  opacity: 0.5;
}

.projects-container {
  padding-top: 30px;
}

.projects {
  overflow: hidden;
  width: 500px;
  height: 530px;
  position: relative;
}

.project {
  display: flex;
  position: absolute;
  top: 0;
  left: 0%;
  width: 100%;
  height: 100%;
  flex-direction: column;
  font-family: 'Share Tech Mono', monospace;
  font-size: 30px;
  font-weight: 100;
  text-align: left;
  pointer-events: none;
  transition: left 0.5s ease-in-out;
}

.project.active {
  pointer-events: auto;
}

.project h3 {
  color: rgba(255, 255, 255, 0);
  transition: color 0.3s ease-in-out;
}

.project-description {
  color: rgba(255, 255, 255, 0);
  transition: color 0.3s ease-in-out;
}

.project p {
  margin: 0;
  margin-bottom: 15px;
}

.project p:last-child {
  margin-bottom: 0;
}

.stack {
  color: rgba(255, 255, 255, 0);
  transition: color 0.3s ease-in-out;
  margin-top: 37px;
}

.project a {
  color: rgba(255, 255, 255, 0) !important;
  transition: color 0.3s ease-in-out;
}

.stack .tool {
  font-size: 25px;
  margin-right: 10px;
}

.stack .tool:last-child {
  margin-right: 0;
}

.project.active a,
.project.active h3,
.project.active .project-description {
  color: rgba(255, 255, 255, 1) !important;
}

.project.active .stack  {
  color: rgba(255, 255, 255, 0.75);
}

.skills {
    display: flex;
    flex-wrap: wrap;
    text-align: left;
    width: 510px;
}

.skills-column {
    padding-left: 20px;
    padding-right: 20px;
}

img {
    vertical-align: bottom;
}

a {
    color: white !important;
}

a:hover {
    opacity: 0.7;
}

.canvas {
    position: fixed;
    top: 0;
    width: 100%;
    height: 100%;
}

.left {
    left: 0;
}

.right {
    right: 0;
}

.center {
    left: 50%;
    transform: translateX(-50%);
}

.card {
    opacity: 0;
    position: relative;
    padding: 60px 60px 70px 60px;
    animation-name: fade-in-anim;
    animation-duration: 0.7s;
    animation-delay: 0.1s;
    animation-timing-function: ease-out;
    animation-fill-mode: forwards;
    background: rgba(13,12,23,0.6);
    border-radius: 20px;
    box-shadow: rgba(50, 50, 93, 0.4) 0px 30px 60px -12px, rgba(0, 0, 0, 0.5) 0px 18px 36px -18px;
}

.letter:hover {
    opacity: 0.5;
    transform: translateY(-10px);
}

@media only screen and (max-width: 1024px) {}

.background-gradient {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-size: 100% 100%;
    
}

.background-animated {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

@media (max-aspect-ratio: 4/3) {
    .background-animated {
        object-fit: cover;
    }
  }

.fade-in {
    opacity: 0;
    animation: fade-in-anim;
    animation-duration: 1s;
    animation-delay: 0.25s;
    animation-fill-mode: forwards;
}

@keyframes fade-in-anim {
    0% {
        opacity: 0;
        transform: translateY(25px);
    }

    50% {
        transform: translateY(-5px);
    }

    100% {
        opacity: 1;
        transform: translateY(0);
    }
}

.scroll-for-more {
    position: fixed;
    bottom: 0;
    left: 50%;
    font-family: 'Share Tech Mono', monospace;
    font-size: 20px;
    font-weight: bold;
    color: white;
    transform: translateX(-50%);
    padding-bottom: 20px;
    transition: all 0.5s ease-in-out;
    opacity: 0;
}

.path {
    stroke-dasharray: 50;
    animation: dash 30s linear;
    animation-iteration-count: infinite;
}

@keyframes dash {
    to {
        stroke-dashoffset: 1000;
    }
}